<template>
  <div>
    <el-container style="height: 700px; border: 2px solid #eee">
      <el-header style="height: 230px; font-size: 40px">
        <el-card
          shadow="hover"
          style="
            background-image: url(https://picx.zhimg.com/v2-39c50c73f90a5731986831f3cb483bd3_1440w.jpg?source=32738c0c);
          "
        >
          InfoPerson
          <div class="block" style="text-align: left">
            <br />
            <el-avatar
              shape="square"
              :size="100"
              src="https://cube.elemecdn.com/9/c2/f0ee8a3c7c9638a54940382568c9dpng.png"
            >
            </el-avatar>
          </div>
        </el-card>
      </el-header>

      <el-main>
        <div>
          <el-skeleton :loading="loading" animated>
            <template slot="template">
              <div style="padding: 14px">
                <el-skeleton :rows="6" animated />
              </div>
            </template>
            <div>
              <h1>基本信息</h1>
              <el-card shadow="hover">
                <div style="padding-left: 14px; padding-right: 90px">
                  <el-row
                    :gutter="20"
                    style="
                      margin-bottom: 20px;
                      &:last-child {
                        margin-bottom: 0;
                      }
                    ">
                    <div style="border-radius: 4px">
                      <el-col :span="6">
                        <b>用户昵称</b>
                      </el-col>
                      <el-col :span="6">
                        <b>用户ID</b>
                      </el-col>
                      <el-col :span="6">
                        <b>性别</b>
                      </el-col>
                      <el-col :span="6">
                        <b>出生日期</b>
                      </el-col>
                    </div>
                  </el-row>
                  <el-row
                    :gutter="20"
                    style="
                      margin-bottom: 20px;
                      &:last-child {
                        margin-bottom: 0;
                      }">
                    <div style="border-radius: 4px">
                      <el-col :span="6">
                        <div id="Nickname">
                          <el-input
                            v-model="nickname"
                            placeholder="请输入昵称"
                          ></el-input>
                        </div>
                      </el-col>
                      <el-col :span="6">
                        <div>{{ userID }}</div>
                      </el-col>
                      <el-col :span="6">
                        <div id="gender">
                          <el-radio v-model="gender" label="1">男</el-radio>
                          <el-radio v-model="gender" label="2">女</el-radio>
                        </div>
                      </el-col>
                      <el-col :span="6">
                        <div id="birthday">
                          <el-date-picker
                            v-model="birthday"
                            type="date"
                            placeholder="选择日期"
                          >
                          </el-date-picker>
                        </div>
                      </el-col>
                    </div>
                  </el-row>
                  <el-row
                    :gutter="20"
                    style="
                      margin-bottom: 20px;
                      &:last-child {
                        margin-bottom: 0;
                      }
                    ">
                    <div style="border-radius: 4px">
                      <el-col :span="6">
                        <b>账户余额</b>
                      </el-col>
                      <el-col :span="6">
                        <b>充值</b>
                      </el-col>
                    </div>
                  </el-row>
                  <el-row
                    :gutter="20"
                    style="
                      margin-bottom: 20px;
                      &:last-child {
                        margin-bottom: 0;
                      }">
                    <div style="border-radius: 4px">
                      <el-col :span="6">
                        <div id="money" class="el-icon-wallet">
                          {{ balanceState.money }}
                          <el-button type="text" class="el-icon-edit-outline" @click="openRechargeDialog">充值</el-button>
                        </div>
                      </el-col>
                      <el-col :span="6">
                        <el-input-number v-model="rechargeAmount" :min="1" :max="10000" placeholder="请输入充值金额"></el-input-number>
                        <el-button type="primary" @click="recharge">确认充值</el-button>
                      </el-col>
                    </div>
                  </el-row>
                </div>
              </el-card>
              <br>
              <el-card shadow="hover">
                <h1>购物足迹</h1>
                <el-table :data="goods" style="width: 100%">
                  <el-table-column prop="goods_name" label="商品名称">
                  </el-table-column>
                  <el-table-column prop="description" label="描述">
                  </el-table-column>
                  <el-table-column prop="add_time" label="添加时间">
                  </el-table-column>
                  <el-table-column
                    fixed="right"
                    label="操作"
                    width="100"
                    type="index"
                    :index="del"
                  >
                    <template>
                      <el-button @click="look_algorithm" type="text" size="small"
                        >查看</el-button
                      >
                    </template>
                  </el-table-column>
                </el-table>
              </el-card>
              <br>
              <el-card shadow="hover">
                <h1>收藏宝贝</h1>
                <el-table :data="message" style="width: 100%">
                  <el-table-column prop="collect_name" label="宝贝名称">
                  </el-table-column>
                  <el-table-column prop="description" label="描述">
                  </el-table-column>
                  <el-table-column prop="add_time" label="添加时间">
                  </el-table-column>
                  <el-table-column
                    fixed="right"
                    label="操作"
                    width="100"
                    type="index"
                    :index="del"
                  >
                    <template>
                      <el-button @click="look_message" type="text" size="small"
                        >查看</el-button
                      >
                    </template>
                  </el-table-column>
                </el-table>
              </el-card>
            </div>
          </el-skeleton>
        </div>
        <br />
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  inject: ['balanceState'], // 注入全局余额状态
  data: () => ({
    userID: "100000001",
    password: "123456",
    rechargeAmount: 0, // 新增：充值金额
    loading: true,
    timerId: null,
    nickname: "Confusoin",
    birthday: "2024-06-28",
    gender: "1",
    goods: [
      { goods_name: "华为 Mate 60 Pro", description: "最新款旗舰手机，搭载麒麟9000S芯片", add_time: "2023-10-01 10:00" },
      { goods_name: "苹果 iPhone 15 Pro Max", description: "A17 Pro芯片，ProMotion超视网膜XDR显示屏", add_time: "2023-10-02 11:00" },
      { goods_name: "三星 Galaxy S23 Ultra", description: "2亿像素主摄，S Pen手写笔支持", add_time: "2023-10-03 12:00" },
      { goods_name: "小米 13 Ultra", description: "徕卡影像，一英寸大底主摄", add_time: "2023-10-04 13:00" },
      { goods_name: "荣耀 Magic5 Pro", description: "MagicOS 7.1系统，1.5K超清护眼屏", add_time: "2023-10-05 14:00" },
    ],
    message: [
      { collect_name: "戴森 V12 Detect Slim", description: "轻量级无线吸尘器，智能探测灰尘", add_time: "2023-10-01 15:00" },
      { collect_name: "索尼 WH-1000XM5", description: "降噪豆耳机，支持LDAC高解析音频", add_time: "2023-10-02 16:00" },
      { collect_name: "飞利浦 329V8QEWAB", description: "4K超高清智能电视，MiniLED背光", add_time: "2023-10-03 17:00" },
      { collect_name: "罗技 MX Master 3S", description: "无线鼠标，支持Flow跨屏操作", add_time: "2023-10-04 18:00" },
      { collect_name: "佳能 EOS R6 Mark II", description: "全画幅无反相机，4000万像素", add_time: "2023-10-05 19:00" },
    ],
    index: "",
  }),
  methods: {
    handleClick(row) {
      console.log(row);
    },
    look_algorithm() {
      this.$router.push("algorithm");
    },
    look_message() {
      this.$router.push("forum");
    },
    openRechargeDialog() {
      this.rechargeAmount = 0; // 初始化充值金额
    },
    recharge() {
      if (this.rechargeAmount <= 0) {
        this.$message.error("请输入有效的充值金额");
        return;
      }
      // 模拟充值逻辑
      const newBalance = parseFloat(this.balanceState.money.replace("￥", "")) + this.rechargeAmount;
      this.balanceState.money = `￥${newBalance.toFixed(2)}`; // 更新全局余额
      this.$message.success(`充值成功，当前余额为 ${this.balanceState.money}`);
    },
  },
  mounted() {
    this.timerId = setTimeout(() => {
      this.loading = false;
    }, 1500);
  },
};
</script>

